import * as React from "react";
import * as ReactDOM from "react-dom";
import {Application} from "~/Application";
// import {dom, library} from "@fortawesome/fontawesome-svg-core";
// import {faImages, faList} from "@fortawesome/free-solid-svg-icons";
import {QueryParamProvider} from "use-query-params";
import {BrowserRouter as Router, Route} from "react-router-dom";
import {Configuration} from "~/models/Configuration";
import {defaultConfiguration} from "~/models/defaultConfiguration";
import {Data} from "~/models/Data";

const fetchConfiguration = (): Promise<Configuration> => {
  return fetch("./configuration.json").then(
    response =>
      response.json().then(
        configuration => configuration,
        () => Promise.resolve(defaultConfiguration)
      ),
    () => Promise.resolve(defaultConfiguration)
  );
};

const fetchData = (): Promise<Data> =>
  fetch("./data.json").then(response =>
    response.text().then(dataJson => ({documents: JSON.parse(dataJson)}))
  );

Promise.all([fetchConfiguration(), fetchData()]).then(
  ([configuration, data]) => {
    console.info("configuration:\n", JSON.stringify(configuration));

    ReactDOM.render(
      <Router>
        <QueryParamProvider ReactRouterRoute={Route}>
          <Application
            configuration={configuration}
            data={data}
          />
        </QueryParamProvider>
      </Router>,
      document.getElementById("root")
    );
  }
);
